import React from "react";
import "bootstrap/dist/css/bootstrap.css";
import { useSelector } from "react-redux";
import ResultsCard from "./ResultsCard";
import "./Components.css";
import { Button } from "react-bootstrap";
import ChoiceTable from "./ChoiceTable";

const ResultsBox = () => {
  const categoriesList = useSelector(
    (state) => state.assessment.present.selectedCategories
  );

  const resultsCSS = useSelector(
    (state) => state.assessment.present.resultsCSS
  );

  function print_current_page() {
    window.print();
  }

  return (
    <div style={resultsCSS}>
      <Button
        onClick={() => window.location.reload()}
        style={{ margin: "15px" }}
      >
        Start a New Assessment
      </Button>
      <br />
      <Button onClick={() => print_current_page()}>Print Your Results</Button>
      <div className="resultsBox">
        {categoriesList.map((item) => {
          return <ResultsCard cardTitle={item} key={Math.random()} />;
        })}
      </div>
      <br />
      <div style={{ margin: "20px" }}>
        <label
          style={{
            fontWeight: "bold",
            fontSize: "large",
            marginBottom: "20px",
          }}
        >
          Review Your Selections
        </label>
        <ChoiceTable />
      </div>
    </div>
  );
};

export default ResultsBox;
