import React from "react";
import { Card, ListGroup, ListGroupItem } from "react-bootstrap";
import { useSelector } from "react-redux";

const ResultsCard = (props) => {
  const resultsList = useSelector((state) => state.assessment.present.result);
  const resourcesList = useSelector(
    (state) => state.assessment.present.resources
  );
  const categoriesList = useSelector(
    (state) => state.assessment.present.selectedCategories
  );

  function findResources() {
    var getR = resourcesList[categoriesList.indexOf(String(props.cardTitle))];
    var getR2 = getR.children;
    var getR3 = [];
    getR3.push(getR2);

    return getR3.flat();
  }

  function imgSelector(title) {
    if (title === "Health Insurance and Medicaid") {
      return "https://media.istockphoto.com/vectors/health-insurance-vector-illustration-medical-protection-medical-vector-id1136667774?k=20&m=1136667774&s=612x612&w=0&h=iJ-t7jGJPsDxQMwX_NnSzsPXqxCDOV7zDI-KT0xZBko=";
    } else if (title === "Disability (SSI/SSDI)") {
      return "https://media.istockphoto.com/vectors/american-security-card-with-its-social-security-number-vector-id1316476874?k=20&m=1316476874&s=612x612&w=0&h=cDl37W_5J4aVN8npGa39B-nR7ruP-f8ddb4wlA5XCuo=";
    } else if (title === "Food Insecurity") {
      return "data:image/jpeg;base64,";
    } else if (title === "WorkFirst") {
      return "data:image/png;base64,";
    } else if (
      title === "Eviction" ||
      "Conditions" ||
      "Loss of Housing Subsidy or Voucher" ||
      "Mortgage Foreclosure" ||
      "Disaster Relief" ||
      "Accommodations/Modifications for Disability" ||
      "Housing Discrimination" ||
      "Other Housing Issue Not Listed"
    ) {
      return "data:image/png;base64,";
    } else if (title === "Debt Collection" || "Repossession of a Vehicle") {
      return "data:image/jpeg;base64,";
    } else if (
      title === "Wills & Powers of Attorney" ||
      "Immigration" ||
      "Expunction" ||
      "Driver's License Restoration"
    ) {
      return "https://media.istockphoto.com/vectors/hand-holding-scales-solid-icon-concept-civil-rights-sign-on-white-vector-id1277251837?k=20&m=1277251837&s=612x612&w=0&h=FMdhs1-eE6_GiTdoR6sAS5GlnKQSE9hD0lkmfEHdJd4=";
    } else if (title === "Interpersonal/Domestic Violence") {
      return "https://ih1.redbubble.net/image.594262905.2484/flat,750x,075,f-pad,750x1000,f8f8f8.u1.jpg";
    } else if (title === "Custody") {
      return "https://pix4free.org/assets/library/2021-01-21/originals/child_custody_law.jpg";
    } else if (
      title === "Problem With Enrollment" ||
      "Suspension or Expulsion" ||
      "Students with Disabilities" ||
      "Discrimination Against a Student" ||
      "Bullying and Harassment"
    ) {
      return "https://i.pinimg.com/originals/9c/84/55/9c845519fece3b72977f0f885a0df851.png";
    } else if (title === "Unemployment Insurance" || "Migrant Workers") {
      return "clip-art-job-career-employment-vector-graphics-png-favpng-RbYG85GNcbhhW6tNKFW11xJa1.jpg";
    }
  }

  var filteredResults = null;
  var cleanResults1 = null;
  var cleanResults2 = null;
  var cleanResults3 = null;
  var cleanResults4 = null;

  const clean = () => {
    if (resultsList.length > 0) {
      filteredResults =
        resultsList[categoriesList.indexOf(String(props.cardTitle))];
      cleanResults1 = String(filteredResults).replaceAll("[", "");
      cleanResults2 = cleanResults1.replaceAll(" [", "");
      cleanResults3 = cleanResults2.replaceAll("];", ";");
      cleanResults4 = cleanResults3.replaceAll("]", "");
      return cleanResults4.split("; ");
    }
  };

  function generateList() {
    if (resultsList.length > 0 && clean()[0].includes("Make a Referral"))
      return (
        <ListGroup className="list-group-flush">
          <ListGroupItem
            key={Math.random()}
            style={{ backgroundColor: "#a0e8a3" }}
          >
            <b>Result:</b>
            <br /> {clean()[0]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Your Action:</b>
            <br /> {clean()[1]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>How LANC Can Help:</b> <br /> {clean()[2]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Other Resources:</b> <br />
            <ol>
              {findResources().map((item) => {
                return (
                  <li>
                    <a
                      href={item.children[0].title}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {item.title}
                    </a>
                  </li>
                );
              })}
            </ol>
          </ListGroupItem>
        </ListGroup>
      );
    else if (resultsList.length > 0 && clean()[0].includes("Unable to Assist"))
      return (
        <ListGroup
          className="list-group-flush"
          style={{ backgroundColor: "#e8a0a0" }}
        >
          <ListGroupItem key={Math.random()}>
            <b>Result:</b>
            <br /> {clean()[0]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Your Action:</b>
            <br /> {clean()[1]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Why LANC Can't Assist:</b> <br /> {clean()[2]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Other Resources:</b> <br />
            <ol>
              {findResources().map((item) => {
                return (
                  <li>
                    <a
                      href={item.children[0].title}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {item.title}
                    </a>
                  </li>
                );
              })}
            </ol>
          </ListGroupItem>
        </ListGroup>
      );
    else if (resultsList.length > 0 && clean()[0].includes("Consultation"))
      return (
        <ListGroup
          className="list-group-flush"
          style={{ backgroundColor: "#fadca5" }}
        >
          <ListGroupItem key={Math.random()}>
            <b>Result:</b>
            <br /> {clean()[0]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Your Action:</b>
            <br /> {clean()[1]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>What's a Consultation?:</b> <br /> {clean()[2]}
          </ListGroupItem>
          <ListGroupItem key={Math.random()}>
            <b>Other Resources:</b> <br />
            <ol>
              {findResources().map((item) => {
                return (
                  <li>
                    <a
                      href={item.children[0].title}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {item.title}
                    </a>
                  </li>
                );
              })}
            </ol>
          </ListGroupItem>
        </ListGroup>
      );
  }

  return (
    <Card
      style={{
        width: "18rem",
        margin: "15px",
      }}
    >
      <Card.Img
        variant="top"
        src={imgSelector(props.cardTitle)}
        style={{
          height: "200px",
          width: "auto",
          objectFit: "cover",
          border: "1px solid black",
        }}
      />
      <Card.Body style={{ flex: "0" }}>
        <Card.Title
          style={{ height: "50px", margin: "auto", padding: "0px 0" }}
        >
          <b>{props.cardTitle}</b>
        </Card.Title>
      </Card.Body>
      {generateList()}
    </Card>
  );
};

export default ResultsCard;
