import "./App.css";
import { Assessment } from "./features/assessment";
import MyProgressBar from "./features/Progressbar";
import Header from "./features/Header";
import ResultsBox from "./features/ResultsBox";
import { useSelector, useDispatch } from "react-redux";
import UndoRedo from "./features/UndoRedo";
import React from "react";
import ConsultBox from "./features/ConsultDirectory";
import Error from "./features/Error";
import Loading from "./features/LoadingScreen";
import Glossary from "./features/Glossary";
import { Tab, Tabs } from "react-bootstrap";
import Title from "./features/Title";
import { UPDATE_TAB } from "./features/assessmentSlice.js";

function App() {
  const displayResults = useSelector((state) => state.assessment.resultsCSS); //why is this not present - is this b/c i am hiding for testing? Did i do this in a dispatch elsewhere?
  const myKey = useSelector((state) => state.assessment.present.tabselect);
  const dispatch = useDispatch();

  return (
    <React.Fragment>
      <div className="App">
        <Loading />
        <Error />
        <Title />

        <Tabs
          id="mytabs"
          style={{ justifyContent: "center", marginTop: "25px" }}
          defaultActiveKey="about"
          activeKey={myKey}
          onSelect={(k) => dispatch(UPDATE_TAB(k))}
          mountOnEnter={true}
          className="myTabs"
        >
          <Tab eventKey="about" title="About this Tool">
            <Header />
          </Tab>
          <Tab eventKey="FAQs" title="Frequently Asked Questions">
            <Glossary />
          </Tab>
          <Tab eventKey="start" title="Assessment">
            <MyProgressBar />
            <br></br>
            <Assessment />
            <br></br>
            <UndoRedo />
            <ResultsBox style={displayResults} />
            <ConsultBox />
          </Tab>
        </Tabs>
      </div>
    </React.Fragment>
  );
}

export default App;
